body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navigation {
  padding-top:10px;
  margin-left:75px;
  margin-top:15px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(255,255,255,.75);
  z-index: 1000; 
  text-align: left;
  padding-left:25px;
  padding-bottom:25px;
}
.img-grid {
  display:grid;
  grid-template-columns: auto auto;
}


.nav-links {
  display: flex;
  flex-wrap: wrap; 
  justify-content: left;
  gap: 10px;
  font-family: 'CustomFont';
  font-size: 24px;
}

.nav-links a {
  text-decoration: none;
  color: black;
  font-size:20px;
}

.nav-links a:hover {
  color: rgba(0,0,0,.5)
}

.page-content {
  padding-top: 200px; 
  text-align: left;
}

.centered-photo {
  width: 100%;
  
}

a{
  /* font-size:50px;
  margin-left:50px; */
}

p {
  margin-left:50px;
  margin-right:50px;
  margin-top:100px;
  max-width:700px;
  padding:5px;
  background: #fff
}

@media (max-width: 427px) {
  .nav-links {
    display: grid;
    grid-template-columns: repeat(3, 0fr); /* Creates a grid with 3 columns */
    grid-template-rows: auto; /* Automatically adjusts the height of rows */
    gap: 10px; /* Space between grid items */
  }

  .nav-links a {
    text-align: left;
  }

  .nav-links a:nth-last-child(1) {
    grid-column: 1 / -1; /* Span the last item across all columns, placing it on a new row */
    text-align: left; /* Align text to the left */
  }
}

@media (min-width: 1050px) and (max-width:1200px) {


  p {
    transform:translateX(100px);
  }

  .navigation {
    transform:translateX(100px);
  }
}

@media (min-width:1200px) and (max-width:1500px) {

  p {
    transform:translateX(150px);
  }

  .navigation {
    transform:translateX(150px);
  }

}

@media (min-width:1500px) and (max-width:2000px) {

  p {
    transform:translateX(300px);
  }

  .navigation {
    transform:translateX(300px);
  }

}

@media (min-width:2000px) {

  p {
    transform:translateX(500px);
  }

  .navigation {
    transform:translateX(500px);
  }

}
